.rc-tree {
    margin: 0;
    overflow-x: auto;
}

.rc-tree .rc-tree-treenode {
    margin: 0;
    padding: 0;
    line-height: 20px;
    white-space: nowrap;
    list-style: none;
    outline: 0;
    cursor: pointer;
}

.rc-tree [role="treeitem"] .draggable {
    color: #333;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;

    -khtml-user-drag: element;
    -webkit-user-drag: element;
}

.rc-tree .rc-tree-treenode.drag-over > .draggable {
    color: white;
    background-color: #316ac5;
    border: 1px #316ac5 solid;
    opacity: 0.8;
}

.rc-tree .rc-tree-treenode.drag-over-gap-top > .draggable {
    border-top: 2px blue solid;
}

.rc-tree .rc-tree-treenode.drag-over-gap-bottom > .draggable {
    border-bottom: 2px blue solid;
}

.rc-tree .rc-tree-treenode.filter-node > .rc-tree-node-content-wrapper {
    color: #a60000 !important;
    font-weight: bold !important;
}

.rc-tree .rc-tree-treenode ul {
    margin: 0;
    padding: 0 0 0 18px;
}

.rc-tree [role="treeitem"] .rc-tree-node-content-wrapper {
    display: inline-table;
    margin: 0;
    padding: 1px 3px 0 0;
    text-decoration: none;
    vertical-align: top;
    cursor: pointer;
}

.rc-tree .rc-tree-treenode span.rc-tree-switcher,
.rc-tree .rc-tree-treenode span.rc-tree-checkbox,
.rc-tree .rc-tree-treenode span.rc-tree-iconEle {
    display: inline-block;
    width: 24px !important;
    height: 24px !important;
    margin-right: 2px;
    line-height: 24px;
    vertical-align: middle;
    background-color: transparent !important;
    background-image: url("data:image/png;base64,") !important;
    background-repeat: no-repeat;
    background-attachment: scroll;
    border: 0 none;
    outline: none;
    cursor: pointer;
}

.rc-tree .rc-tree-treenode span.rc-tree-switcher.rc-tree-icon__customize,
.rc-tree .rc-tree-treenode span.rc-tree-checkbox.rc-tree-icon__customize,
.rc-tree .rc-tree-treenode span.rc-tree-iconEle.rc-tree-icon__customize {
    background-image: none;
}

.rc-tree .rc-tree-treenode span.rc-tree-icon_loading {
    margin-right: 2px;
    vertical-align: top;
    background: url("data:image/gif;base64,") no-repeat scroll 0 0 transparent;
}

.rc-tree .rc-tree-treenode span.rc-tree-switcher.rc-tree-switcher-noop {
    cursor: auto;
}

.rc-tree .rc-tree-treenode:first-child span.rc-tree-switcher.rc-tree-switcher_open {
    background-position: -131px -3px;
}

.rc-tree .rc-tree-treenode:not(:first-child) span.rc-tree-switcher.rc-tree-switcher_open {
    background-position: -132px -5px;
}

.rc-tree .rc-tree-treenode:first-child span.rc-tree-switcher.rc-tree-switcher_close {
    background-position: -99px -3px;
}
.rc-tree .rc-tree-treenode:not(:first-child) span.rc-tree-switcher.rc-tree-switcher_close {
    background-position: -100px -5px;
}

.rc-tree [role="treeitem"] span.rc-tree-checkbox {
    width: 13px;
    height: 13px;
    margin: 0 3px;
    background-position: 0 0;
}

.rc-tree [role="treeitem"] span.rc-tree-checkbox-checked {
    background-position: -14px 0;
}

.rc-tree [role="treeitem"] span.rc-tree-checkbox-indeterminate {
    background-position: -14px -28px;
}

.rc-tree [role="treeitem"] span.rc-tree-checkbox-disabled {
    background-position: 0 -56px;
}

.rc-tree
[role="treeitem"]
span.rc-tree-checkbox.rc-tree-checkbox-checked.rc-tree-checkbox-disabled {
    background-position: -14px -56px;
}

.rc-tree
[role="treeitem"]
span.rc-tree-checkbox.rc-tree-checkbox-indeterminate.rc-tree-checkbox-disabled {
    position: relative;
    background: #ccc;
    border-radius: 3px;
}

.rc-tree [role=treeitem] span.rc-tree-switcher.rc-tree-switcher_close {
    border: none;
}
.rc-tree
[role="treeitem"]
span.rc-tree-checkbox.rc-tree-checkbox-indeterminate.rc-tree-checkbox-disabled::after {
    position: absolute;
    top: 5px;
    left: 3px;
    width: 5px;
    height: 0;
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
    -webkit-transform: scale(1);
    transform: scale(1);
    content: " ";
}

.rc-tree:not(.rc-tree-show-line) .rc-tree-treenode .rc-tree-switcher-noop {
    background: none;
}

.rc-tree.rc-tree-show-line .rc-tree-treenode:not(:last-child) > ul {
    /* background: url("data:image/gif;base64,R0lGODlhCQACAIAAAMzMzP///yH5BAEAAAEALAAAAAAJAAIAAAIEjI9pUAA7") 0 0 repeat-y; */
    content: "└";
}

.rc-tree.rc-tree-show-line .rc-tree-treenode:not(:last-child) > .rc-tree-switcher-noop {
    background-position: -68px -5px;
}

.rc-tree.rc-tree-show-line .rc-tree-treenode:last-child > .rc-tree-switcher-noop {
    background-position: -69px -36px;
    transform: rotate(90deg);
}

.rc-tree-child-tree {
    display: none;
}

.rc-tree-child-tree-open {
    display: block;
}

.rc-tree-treenode-disabled > span:not(.rc-tree-switcher),
.rc-tree-treenode-disabled > a,
.rc-tree-treenode-disabled > a span {
    color: #767676;
    cursor: not-allowed;
}

.rc-tree-node-selected {
    background-color: #EBEDF2;
    border: 0;
    opacity: 1;
    padding: 0px 3px 0px 3px !important;
    border-radius: 5px;
}

.rc-tree-icon__open {
    margin-right: 2px;
    vertical-align: top;
    background-position: -110px -16px;
}

.rc-tree-icon__close {
    margin-right: 2px;
    vertical-align: top;
    background-position: -110px 0;
}

.rc-tree-icon__docu {
    margin-right: 2px;
    vertical-align: top;
    background-position: -110px -32px;
}

.rc-tree-icon__customize {
    margin-right: 2px;
    vertical-align: top;
}

.rc-tree-indent-unit {
    display: inline-block;
    padding-left: 18px;
}

.rc-tree-indent-unit:first-child {
    display: inline-block;
    padding-left: 18px !important;
}


.react-contextmenu {
    background-color: #fff;
    -webkit-box-shadow: 0 0 15px 1px rgb(81 77 92 / 20%);
    box-shadow: 0 0 15px 1px rgb(81 77 92 / 20%);
    border-radius: 4px;
    padding: 10px;
    width: 245px;
    z-index: 2;
}

.react-contextmenu .react-contextmenu-item {
    display: -webkit-box;
    table-layout: fixed;
    text-decoration: none;
    position: relative;
    outline: 0 !important;
    vertical-align: middle;
    padding: 5px 0;
    cursor: pointer;
    color: #575962;
}

.react-contextmenu .react-contextmenu-item:hover {
    color: #ff5b1d;
}

.react-contextmenu .react-contextmenu-item .m-nav__link-icon {
    display: block;
    height: 100%;
    vertical-align: middle;
    text-align: left;
    width: 30px;
    font-size: 1.8rem !important;
}

.react-contextmenu .react-contextmenu-item .m-nav__link-text {
    display: table-cell;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    font-size: 1.2rem;
}

/***************** Tree switcher icon style **************/
.icon-with-tree .rc-tree.rc-tree-show-line .rc-tree-list .rc-tree-title {
    color: black;
    margin-left: 10px;
}

.icon-with-tree .rc-tree.rc-tree-show-line .rc-tree-list .rc-tree-switcher img {
    position: relative;
    left: 14px;
    top: 2px;
}

.rc-tree-title {
    color: #505050;
    font-size: 14px !important;
    font-family: Mulish;
}

